<template>
  <div id="SupervisorPage">
    <div class="addNewTask">
      <h2>新增任务</h2>
      <div>
        <el-form :model="taskForm" :inline="true">
          <el-form-item label="国家">
            <el-select
              @change="changeOpt1"
              v-model="taskForm.country"
              placeholder="请选择国家"
            >
              <el-option
                v-for="(item, index) in task_countries"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <!-- @change="changeOpt('country', taskForm)" -->
            <el-select v-model="taskForm.station" placeholder="请选择站点">
              <el-option
                v-for="(item, index) in task_stations"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库">
            <el-select
              v-model="taskForm.warehouse_name"
              placeholder="请选择需要更改的仓库"
            >
              <el-option
                v-for="(item, index) in task_warehouses"
                :key="index"
                :label="item.warehouse_name"
                :value="item.warehouse_name"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="更换原因">
            <el-input
              placeholder="请输入更换原因"
              type="text"
              v-model="taskForm.reason"
            ></el-input>
          </el-form-item>
          <el-form-item label="模板类型">
            <el-select
              v-model="taskForm.template_type"
              placeholder="请选择模板类型"
            >
              <el-option
                v-for="(item, index) in task_templateTypes"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click="addNewTask" type="primary"
              >更改仓库模板</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="checkTemplateUpdate">
      <h2>模板更改审核</h2>
      <div>
        <el-table
          border
          :data="templateTable"
          style="width: 80%;margin:0 auto;"
          max-height="300"
        >
          <el-table-column align="center" prop="country" label="国家">
          </el-table-column>
          <el-table-column align="center" prop="warehouse_name" label="仓库">
          </el-table-column>
          <el-table-column align="center" prop="template_type" label="模板类型">
          </el-table-column>
          <el-table-column align="center" prop="update_times" label="更新时间">
          </el-table-column>
          <el-table-column align="center" prop="update_reason" label="更新原因">
          </el-table-column>
          <el-table-column align="center" label="操作">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="approved(scope.$index, templateTable)"
                type="primary"
                size="small"
              >
                通过
              </el-button>
              <el-button
                @click.native.prevent="reject(scope.$index, templateTable)"
                type="danger"
                size="small"
              >
                驳回
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div class="templateChange">
      <h2>模板更改</h2>
      <div>
        <el-form :model="templateChangeForm" :inline="true">
          <el-form-item label="国家">
            <el-select
              @change="changeOpt2"
              v-model="templateChangeForm.country"
              placeholder="请选择国家"
            >
              <el-option
                v-for="(item, index) in templateChange_countries"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select
              v-model="templateChangeForm.station"
              placeholder="请选择站点"
            >
              <el-option
                v-for="(item, index) in templateChange_stations"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库">
            <el-select
              v-model="templateChangeForm.warehouse_name"
              placeholder="请选择需要新增的仓库"
            >
              <el-option
                v-for="(item, index) in templateChange_warehouses"
                :key="index"
                :label="item.warehouse_name"
                :value="item.warehouse_name"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="模板">
            <el-select
              v-model="templateChangeForm.template_type"
              placeholder="请选择需要模板"
            >
              <el-option
                v-for="(item, index) in templateChange_templateTypes"
                :key="index"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getTemplate">模板搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="templateModel">
        <div class="complete_template">
          <el-input
            v-model="templateStepList[0].title"
            placeholder="请输入模板标题,示例：（自动化，加急，隔日达，当天达 + 1天备货时间）"
          ></el-input>
          <div class="stepCard" v-for="(item, i) in templateStepList" :key="i">
            <div v-if="i > 0">
              <p class="step">步骤{{ item.step }}</p>
              <el-input
                type="text"
                v-model="item.title"
                :placeholder="hTitle_placeholder(i)"
              ></el-input>
              <el-input
                type="textarea"
                v-model="item.content"
                :placeholder="hTip_placeholder(i)"
              ></el-input>
              <div class="img_con">
                <img
                  v-for="(small_item, j) in item.images.split(',')"
                  :key="j"
                  :src="
                    '/home/beyoung_operate/static/data/template_datas/' +
                      templateChangeForm.station +
                      '/' +
                      templateChangeForm.country +
                      '/' +
                      templateChangeForm.warehouse_name +
                      '/' +
                      templateChangeForm.template_type +
                      '/' +
                      small_item
                  "
                  alt=""
                />
              </div>
              <div class="confirm">
                <el-upload
                  :ref="'uploadImg' + item.step"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  list-type="picture-card"
                  :auto-upload="false"
                  drag
                  multiple
                >
                  <i slot="default" class="el-icon-plus"></i>
                  <div slot="file" slot-scope="{ file }">
                    <img
                      class="el-upload-list__item-thumbnail"
                      :src="file.url"
                      alt=""
                    />
                    <span class="el-upload-list__item-actions">
                      <span
                        class="el-upload-list__item-preview"
                        @click="handlePictureCardPreview(file)"
                      >
                        <i class="el-icon-zoom-in"></i>
                      </span>
                      <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleRemove(file, 'uploadImg' + i)"
                      >
                        <i class="el-icon-delete"></i>
                      </span>
                    </span>
                  </div>
                </el-upload>
                <el-dialog :visible.sync="dialogVisible">
                  <img width="100%" :src="dialogImageUrl" alt="" />
                </el-dialog>
              </div>
            </div>
          </div>
        </div>
        <div class="submitBtn">
          <el-button @click="submitFormData" type="primary">提交模板</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import StepCardSupervisor from "@/components/orderManagement/FBM/StepCardSupervisor";
export default {
  data() {
    return {
      // 新增任务
      taskForm: {
        warehouse_name: "",
        template_type: "",
        country: "",
        reason: "",
        station: ""
      },
      task_warehouses: [],
      task_countries: [],
      task_stations: [],
      task_templateTypes: [],
      // 检查模板更改
      templateTable: [],
      // 模板更改
      templateChangeForm: {
        warehouse_name: "",
        template_type: "",
        country: "",
        station: ""
      },
      templateChange_stations: [],
      templateChange_countries: [],
      templateChange_warehouses: [],
      templateChange_templateTypes: [],
      // 模板示例数据
      templateStepList: [
        {
          id: 1,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "0",
          title: "（自动化，加急，隔日达，当天达 + 1天备货时间）",
          content: null,
          images: null
        },
        {
          id: 2,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "1",
          title: "1、设置仓库",
          content:
            "点击设置-配送设置-一般配送设置-编辑-添加新地址，填写仓库地址 美东仓库地址：12 McCullough Dr New Castle，DE 19720,New Castle, DE, 19720, 美国",
          images: "mdc_step1.png"
        },
        {
          id: 3,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "2",
          title: "2、配送模板",
          content: "创建新配送模板并命名",
          images: "mdc_step2.png"
        },
        {
          id: 4,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "3",
          title: "3、设置模板",
          content: "选择美东仓库-下一步-选择承运人配送服务（此处为FedEx配送）",
          images: "mdc_step3_1.png,mdc_step3_2.png"
        },
        {
          id: 5,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "4",
          title: "4.下一步-完成",
          content:
            "勾选自动化，标准运输时间，设置为2-4天，偏远地区为5-8天，且运费为4.99，每磅0.99刀",
          images: "mdc_step4_1.png,mdc_step4_2.png"
        },
        {
          id: 6,
          station: "胤佑",
          country: "美国",
          warehouse_name: "品晟美东仓",
          template_type: "美东仓标准模板",
          step: "5",
          title: "5、库存-商品页面-选择相应的配送模板-报价-处理时间-1天",
          content: "",
          images: "mdc_step5.png"
        }
      ],
      // 模板更改架构内容
      templateStepList_: {},
      // 上传步骤图片
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false
    };
  },
  methods: {
    // 新增任务
    addNewTask() {
      let url = this._url2 + "server/order_management/add_work/";
      let params = {};
      for (let key in this.taskForm) {
        if (this.taskForm[key].length === 0) {
          alert("请选择渠道站点国家更改原因及模板类型！");
          return;
        }
        params[key] = this.taskForm[key];
        // formData.append(key, this.taskForm[key]);
      }
      this.$axios
        .get(url, {
          params
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            alert("提交成功！");
            this.taskForm.warehouse_name = "";
            this.taskForm.template_type = "";
            this.taskForm.country = "";
            this.taskForm.reason = "";
            this.taskForm.station = "";
          } else {
            alert("提交未成功！");
          }
        });
    },
    // 模板更改
    approved(index, data) {
      // 通过
      console.log(index, data);
      let item = data[index];
      let url =
        this._url2 + "server/order_management/examined_template_update/";
      this.$axios
        .get(url, {
          params: {
            id: item.id,
            station: item.station,
            country: item.country,
            warehouse_name: item.warehouse_name
          }
        })
        .then(res => {
          this.templateTable = res.data.data;
        });
    },
    reject(index, data) {
      // 驳回
      console.log(index, data);
    },
    hTitle_placeholder(idx) {
      let title = "请输入步骤标题，示例：" + this.templateStepList_[idx].title;
      return title;
    },
    hTip_placeholder(idx) {
      let title =
        "请输入步骤标题，示例：" + this.templateStepList_[idx].content;

      return title;
    },
    changeOpt1() {
      // todo
      this.task_warehouses = [];
      this.task_stations = [];
      this.taskForm.warehouse_name = "";
      this.taskForm.station = "";
      let url2 = this._url2 + "server/order_management/warehouse_datas/";
      this.$axios
        .get(url2, {
          params: {
            country: this.taskForm.country
          }
        })
        .then(res => {
          this.task_warehouses = res.data.data;
          // this.templateChange_warehouses = res.data.data;
        });
      let url3 = this._url2 + "server/personnel_management/get_sign_area/";
      this.$axios
        .get(url3, {
          params: {
            country: this.taskForm.country
          }
        })
        .then(res => {
          this.task_stations = res.data.data;
        });
    },
    changeOpt2() {
      // todo
      //   this.task_warehouses = [];
      // this.task_stations = [];
      // this.taskForm.warehouse_name = "";
      // this.taskForm.station = "";
      let url2 = this._url2 + "server/order_management/warehouse_datas/";
      this.$axios
        .get(url2, {
          params: {
            country: this.templateChangeForm.country
          }
        })
        .then(res => {
          this.templateChange_warehouses = res.data.data;
        });
      let url3 = this._url2 + "server/personnel_management/get_sign_area/";
      this.$axios
        .get(url3, {
          params: {
            country: this.templateChangeForm.country
          }
        })
        .then(res => {
          this.templateChange_stations = res.data.data;
        });
    },
    // 图片上传
    handleRemove(file, fileItem) {
      console.log(this.$refs[fileItem][0].uploadFiles);
      console.log(fileItem);
      // 比较 uid: 1609320925411
      // this.$refs.uploadImg.uploadFiles
      let fileList = this.$refs[fileItem][0].uploadFiles;
      console.log(file.uid, fileList);
      let index = fileList.findIndex(i => {
        return i.uid === file.uid;
      });
      fileList.splice(index, 1);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    submitImg(uploadItem) {
      this.$refs[uploadItem][0].submit();
    },
    submitFormData() {
      let fd = new FormData();
      let dataArr = [];
      for (let i in this.templateStepList) {
        let obj = {};
        let item = this.templateStepList[i];
        for (let key in item) {
          if (key === "id" || key === "images") {
            continue;
          }
          obj[key] = item[key];
        }
        dataArr.push(obj);
      }
      let dataStr = JSON.stringify(dataArr);
      fd.append("data", dataStr);
      console.log("打印数据", fd);
      let len = this.templateStepList.length;
      for (let i = 1; i < len; i++) {
        let files = this.$refs["uploadImg" + i][0].uploadFiles;
        files.forEach(file => {
          console.log(file);
          fd.append("files" + i, file.raw);
        });
      }
      let url = "server/order_management/template_datas_update/";
      this.$axios.post(this._url2 + url, fd).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          alert("修改模板成功！");
          // 渲染新的模板？？？
        } else {
          alert("提交出错！");
        }
      });
    },
    // 请求参数
    getSelect() {
      let tempStr = JSON.stringify(this.templateStepList);
      this.templateStepList_ = JSON.parse(tempStr);
      // 国家
      let url1 = this._url2 + "server/personnel_management/get_sign_country/";
      this.$axios.get(url1).then(res => {
        this.task_countries = res.data.data;
        this.templateChange_countries = res.data.data;
      });
      let url2 = this._url2 + "server/order_management/get_template/";
      this.$axios.get(url2).then(res => {
        this.task_templateTypes = res.data.data;
        this.templateChange_templateTypes = res.data.data;
      });
    },
    // 请求检查模板更改的数据
    getData() {
      let url = this._url2 + "server/order_management/get_examined_data/";
      this.$axios.get(url).then(res => {
        this.templateTable = res.data.data;
      });
    },
    getTemplate() {
      for (let key in this.templateChangeForm) {
        if (!this.templateChangeForm[key]) {
          alert("请选择国家站点仓库名及模板类型");
          return;
        }
      }
      let url = "server/order_management/template_datas/";
      this.$axios
        .get(this._url2 + url, {
          params: {
            station: this.templateChangeForm["station"],
            country: this.templateChangeForm["country"],
            warehouse_name: this.templateChangeForm["warehouse_name"],
            template_type: this.templateChangeForm["template_type"]
          }
        })
        .then(res => {
          console.log(res);
          this.templateStepList = res.data.data;
        });
    }
  },
  components: {
    // StepCardSupervisor
  },
  created() {
    this.getSelect();
    this.getData();
  }
};
</script>

<style scoped>
#SupervisorPage h2 {
  font-size: 20px;
  padding: 20px 0;
}
#SupervisorPage .addNewTask,
#SupervisorPage .checkTemplateUpdate,
#SupervisorPage .templateChange {
  margin: 0 auto;
  text-align: center;
  width: 80vw;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  margin-top: 20px;
}
#SupervisorPage .checkTemplateUpdate {
  padding-bottom: 22px;
}
#SupervisorPage .complete_template {
  width: 75%;
  margin: 0 auto;
}
#SupervisorPage .templateModel .step {
  text-align: left;
  margin: 20px 0;
}
#SupervisorPage .templateModel >>> textarea {
  margin: 20px 0;
}
#SupervisorPage .complete_template .img_con > img {
  width: 70%;
  margin-bottom: 30px;
}
#SupervisorPage >>> .el-upload__input {
  display: none;
}
#SupervisorPage .submitBtn >>> .el-button {
  margin-top: 20px;
}
</style>
<style>
#SupervisorPage .checkTemplateUpdate .el-table td.is-center,
#SupervisorPage .checkTemplateUpdate .el-table th.is-center {
  height: 30px;
}
#SupervisorPage .checkTemplateUpdate .el-button--small,
#SupervisorPage .checkTemplateUpdate .el-button--small.is-round {
  padding: 5px 15px;
}
#SupervisorPage .checkTemplateUpdate .el-table .cell,
#SupervisorPage .checkTemplateUpdate .el-table--border td:first-child .cell,
#SupervisorPage .checkTemplateUpdate .el-table--border th:first-child .cell {
  padding-left: 0;
}
#SupervisorPage .confirm .el-upload-dragger {
  width: 100%;
  height: 100%;
}
</style>



